html {
  scroll-behavior: smooth
}
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "SpaceGrotesk";
  src: local("SpaceGrotesk"), url(SpaceGrotesk.woff) format("woff");
}

body {
  margin: 0;
  font-family: "SpaceGrotesk", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: "SpaceGrotesk", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
  "Droid Sans", "Helvetica Neue", sans-serif
}

body::-webkit-scrollbar {
  /* display: none; */
  width: 5px;
  background-color: #17161b;
}

body::-webkit-scrollbar-thumb {
  background-color: var(--secondary);
  border-radius: 5px;
}

body {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

html {
  background-color: "#4C4C4C";
}

div {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -o-user-select: none;
}

.shapeshadow{
  width: 40px;
  height: 40px;
  background-color: #17161b;
  position: absolute;
  margin: 5px 0 0 0;
}

.shapedone{
  width: 15px;
  height: 15px;
  background-color: var(--secondary);
  transform: rotate(45deg);
  position: absolute;
  margin: 12px 0 0 0;
}

.shape{
  width: 15px;
  height: 15px;
  background-color: white;
  transform: rotate(45deg);
  position: absolute;
  margin: 12px 0 0 0;
}

.linedone{
  width: 3px;
  height: 170px;
  background-color: var(--secondary);
  position: absolute;
  margin: 0 0 0 -1px;
}

.line{
  width: 1px;
  height: 100%;
  background-color: hsla(0,0%,100%,.4);
  position: absolute;
  margin: -150px 0 0 7px;
}

.footline{
  width: 100%;
  height: 1px;
  background-color: hsla(0,0%,100%,.1);
}

a:link { text-decoration: none; color: white}
a:hover { text-decoration: underline; color: white} 
